import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { GraduateProjectType} from '../entity';
import { UniversityService } from '../university.service';

@Component({
  selector: 'app-update',
  templateUrl: './update-graduateProjectType.component.html',
  styleUrls: ['./update-graduateProjectType.component.css']
})
export class UpdateGraduateProjectTypeComponent implements OnInit {
  // 前后端双向绑定
  @Input() graduateProjectType: GraduateProjectType;

  constructor(
    private route: ActivatedRoute,
    private graduateProjectTypeService: UniversityService,
    private location: Location,
  ) {
    this.route.params.subscribe(data => console.log(data.mid));
  }

  ngOnInit(): void {
    // 获取路由传递过来的参数id
    const id = this.route.snapshot.params.id;
    // 获得当前需要修改的数据，并赋给this.college
    this.graduateProjectTypeService.getGraduateProjectType (id)
      .subscribe(res => this.graduateProjectType = res);
    /****
     this.route.queryParams
     .subscribe((params: College) => {
        this.college = params;
      });
     ******/

  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.graduateProjectTypeService.updateGraduateProjectType(this.graduateProjectType)
      .subscribe(() => this.goBack());
  }
}
