import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddCollegeComponent } from './college/add-college.component';
import { UpdateCollegeComponent } from './college/update-college.component';
import { CollegeComponent } from './college/college.component';
import { TeacherComponent } from './teacher/teacher.component';
import { DepartmentComponent} from './department/department.component';
import { AddDepComponent } from './department/add-dep.component';
import { UpdateDepComponent } from './department/update-dep.component';
import { AddTeacherComponent } from './teacher/add-teacher.component';
import { UpdateTeacherComponent } from './teacher/update-teacher.component';
import {DegreeComponent} from './degree/degree.component';
import {AddDegreeComponent } from './degree/add-degree.component';
import {UpdateDegreeComponent } from './degree/update-degree.component';
import { ProfTitleComponent} from './proftitle/proftitle.component';
import {AddTitleComponent } from './proftitle/add-title.component';
import {UpdateTitleComponent } from './proftitle/update-title.component';
import {GraduateProjectComponent} from './GraduatProject/graduateProject.component';
import {GraduateProjectCategoryComponent} from './graduateCategory/graduateProjectCategory.component';
import {AddGraduateProjectCategoryComponent} from './graduateCategory/add-graduateProjectCategory.component';
import {UpdateGraduateProjectCategoryComponent} from './graduateCategory/update-graduateProjectCategory.component';
import {AddGraduateProjectStatusComponent} from './graduateStatus/add-graduateProjectStatus.component';
import {UpdateGraduateProjectStatusComponent} from './graduateStatus/update-graduateProjectStatus.component';
import {AddGraduateProjectTypeComponent} from './graduateType/add-graduateProjectType.component';
import {UpdateGraduateProjectTypeComponent} from './graduateType/update-graduateProjectType.component';
import {AddGraduateProjectComponent} from './GraduatProject/add-graduateProject.component';
import {UpdateGraduateProjectComponent} from './GraduatProject/update-graduateProject.component';
import {GraduateProjectStatusComponent} from './graduateStatus/graduateProjectStatus.component';
import {GraduateProjectTypeComponent} from './graduateType/graduateProjectType.component';
// tslint:disable-next-line:import-spacing
import { LoginComponent } from  './login/login.component';
import { IndexComponent } from './index/index.component';

 //import {LogoutComponent} from './login/logout.component';


const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'},
 // {path: 'logout', component: LogoutComponent}, // (空路径）默认路由(登录)
  {path: 'index', component: IndexComponent, children: [ // 嵌套路由
      { path: '', redirectTo: '/college', pathMatch: 'full' }, // 空路径 重定向
      { path: 'college', component: CollegeComponent},
      { path: 'degree', component: DegreeComponent},
      { path: 'title', component: ProfTitleComponent},
      { path: 'department', component: DepartmentComponent},
      { path: 'teacher', component: TeacherComponent},
      { path: 'graduateProject', component: GraduateProjectComponent},
      { path: 'addCollege', component: AddCollegeComponent},
      { path: 'updateCollege/:id', component: UpdateCollegeComponent},
      { path: 'addDep', component: AddDepComponent},
      { path: 'updateDep/:id', component: UpdateDepComponent},
      { path: 'addTeacher', component: AddTeacherComponent},
      { path: 'updateTeacher/:id', component: UpdateTeacherComponent},
      { path: 'addDegree', component: AddDegreeComponent},
      { path: 'updateDegree/:id', component: UpdateDegreeComponent},
      { path: 'addTitle', component: AddTitleComponent},
      { path: 'updateTitle/:id', component: UpdateTitleComponent},
      { path: 'graduateProjectCategory', component: GraduateProjectCategoryComponent},
      { path: 'graduateProjectStatus', component: GraduateProjectStatusComponent},
      { path: 'graduateProjectType', component: GraduateProjectTypeComponent},
      { path: 'addGraduateProjectCategory', component: AddGraduateProjectCategoryComponent},
      { path: 'updateGraduateProjectCategory/:id', component: UpdateGraduateProjectCategoryComponent},
      { path: 'addGraduateProjectStatus', component: AddGraduateProjectStatusComponent},
      { path: 'updateGraduateProjectStatus/:id', component: UpdateGraduateProjectStatusComponent},
      { path: 'addGraduateProjectType', component: AddGraduateProjectTypeComponent},
      { path: 'updateGraduateProjectType/:id', component: UpdateGraduateProjectTypeComponent},
      { path: 'addGraduateProjectProject', component: AddGraduateProjectComponent},
      { path: 'updateGraduateProjectProject/:id', component: UpdateGraduateProjectComponent}
      ]},
];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
