import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { GraduateProjectCategory} from '../entity';
import { UniversityService } from '../university.service';

@Component({
  selector: 'app-update',
  templateUrl: './update-graduateProjectCategory.component.html',
  styleUrls: ['./update-graduateProjectCategory.component.css']
})
export class UpdateGraduateProjectCategoryComponent implements OnInit {
  // 前后端双向绑定
  @Input() graduateProjectCategory: GraduateProjectCategory;

  constructor(
    private route: ActivatedRoute,
    private graduateProjectCategoryService: UniversityService,
    private location: Location,
  ) {
    this.route.params.subscribe(data => console.log(data.mid));
  }

  ngOnInit(): void {
    // 获取路由传递过来的参数id
    const id = this.route.snapshot.params.id;
    // 获得当前需要修改的数据，并赋给this.college
    this.graduateProjectCategoryService.getGraduateProjectCategory (id)
      .subscribe(res => this.graduateProjectCategory = res);
    /****
     this.route.queryParams
     .subscribe((params: College) => {
        this.college = params;
      });
     ******/

  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.graduateProjectCategoryService.updateGraduateProjectCategory(this.graduateProjectCategory)
      .subscribe(() => this.goBack());
  }
}
