import { Component, OnInit, Input} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { UniversityService} from '../university.service';
import {
  GraduateProject,
  Degree,
  Title,
  College,
  Department,
  Teacher,
  GraduateProjectType,
  GraduateProjectStatus, GraduateProjectCategory
} from '../entity';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-update-graduateProject',
  templateUrl: './update-graduateProject.component.html',
})
export class UpdateGraduateProjectComponent implements OnInit {
  @Input() schools: College[];
  degrees: Degree[];
  titles: Title[];
  departments: Department[];
  teachers: Teacher[];
  graduateProjectTypes: GraduateProjectType[];
  graduateProjectStatuss: GraduateProjectStatus[];
  graduateProjectCategorys: GraduateProjectCategory[];
  graduateProject: GraduateProject;
  graduateProjectType: GraduateProjectType;
  graduateProjectStatus: GraduateProjectStatus;
  graduateProjectCategory: GraduateProjectCategory;
  school: College;
  degree: Degree;
  title: Title;
  department: Department;
  teacher: Teacher;
  constructor(
    private route: ActivatedRoute,
    private graduateProjectService: UniversityService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getColleges() ;
    this.getDegrees();
    this.getTitles();
    this.getDepartments();
    this.getGraduateProjectTypes();
    this.getGraduateProjectStatuss();
    this.getGraduateProjectCategorys();
    this.getTeachers();
    const id = this.route.snapshot.params.id; // 要修改专业的id
    this.graduateProjectService.getGraduateProject(id)
      .subscribe(res => {
        this.graduateProject = res;
      });
  }
  getColleges(): void {
    this.graduateProjectService.getColleges ()
      .subscribe(res => this.schools = res);
  }
  getDegrees(): void {
    this.graduateProjectService.getDegrees ()
      .subscribe(res => this.degrees = res);
  }
  getTitles(): void {
    this.graduateProjectService.getTitles ()
      .subscribe(res => this.titles = res);
  }
  getDepartments(): void {
    this.graduateProjectService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  getGraduateProjectTypes(): void {
    this.graduateProjectService.getGraduateProjectTypes ()
      .subscribe(res => this.graduateProjectTypes = res);
  }
  getGraduateProjectStatuss(): void {
    this.graduateProjectService.getGraduateProjectStatuss ()
      .subscribe(res => this.graduateProjectStatuss = res);
  }
  getGraduateProjectCategorys(): void {
    this.graduateProjectService.getGraduateProjectCategorys ()
      .subscribe(res => this.graduateProjectCategorys = res);
  }
  getTeachers(): void {
    this.graduateProjectService.getTeachers ()
      .subscribe(res => this.teachers = res);
  }

  save(): void { // 多个文本框数据
    this.graduateProjectService.updateGraduateProject(this.graduateProject)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
  compareFn(o1: College, o2: College): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

}
