// service类用来处理数据，获取数据，与后台进行交互 ng g service school
import { Injectable } from '@angular/core';
// 实现Http通信(get/post/put/delete)
import { HttpClient, HttpHeaders } from '@angular/common/http';
// 监听来自后台的数据
import { Observable, of } from 'rxjs';
// 异常处理
import { catchError, map, tap } from 'rxjs/operators';
// 引入实体类，使用实体类中定义的属性
// tslint:disable-next-line:max-line-length
import {
  College,
  Degree,
  Department,
  Department1, GraduateProject, GraduateProjectCategory, GraduateProjectStatus,
  GraduateProjectType,
  Response,
  Teacher,
  Teacher1,
  Title
} from './entity';
// 中间传输的选项数据（http传输，json格式）
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};




@Injectable({
  providedIn: 'root'
})
export class UniversityService {
  private userUrl = 'http://localhost:8080/login';
  private gradProjectUrl = 'http://localhost:8080/graduateProject.ctl';
  private projectStatusUrl = 'http://localhost:8080/graduateProjectStatus.ctl';
  private projectTypeUrl = 'http://localhost:8080/graduateProjectType.ctl';
  private projectCategoryUrl = 'http://localhost:8080/graduateProjectCategory.ctl';
  private collegeUrl = 'http://localhost:8080/school.ctl';  // 后台servlet中映射的url
  private depUrl = 'http://localhost:8080/department.ctl';
  private teacherUrl = 'http://localhost:8080/teacher.ctl';
  private degreeUrl = 'http://localhost:8080/degree.ctl';
  private titleUrl = 'http://localhost:8080/profTitle.ctl';
  // 接口，获取后端所提供的数据
  constructor(private http: HttpClient) { }

  /** GET schools from the server */
  // College类型的json数组（school组件调用）
  getColleges(): Observable<College[]> {
    return this.http.get<College[]>(this.collegeUrl )
      .pipe(
        catchError(this.handleError<College[]>('getColleges', []))
      );
  }
  getGraduateProjectCategorys(): Observable<GraduateProjectCategory[]> {
    return this.http.get<GraduateProjectCategory[]>(this.projectCategoryUrl )
      .pipe(
        catchError(this.handleError<GraduateProjectCategory[]>('getProjectStatus', []))
      );
  }
  getGraduateProjectTypes(): Observable<GraduateProjectType[]> {
    return this.http.get<GraduateProjectType[]>(this.projectTypeUrl )
      .pipe(
        catchError(this.handleError<GraduateProjectType[]>('getProjectTypes', []))
      );
  }
  getGraduateProjects(): Observable<GraduateProject[]> {
    return this.http.get<GraduateProject[]>(this.gradProjectUrl )
      .pipe(
        catchError(this.handleError<GraduateProject[]>('getGradProjects', []))
      );
  }
  getGraduateProjectStatuss(): Observable<GraduateProjectStatus[]> {
    return this.http.get<GraduateProjectStatus[]>(this.projectStatusUrl )
      .pipe(
        catchError(this.handleError<GraduateProjectStatus[]>('getProjectStatuss', []))
      );
  }
  getDegrees(): Observable<Degree[]> {
    return this.http.get<Degree[]>(this.degreeUrl)
      .pipe(
        catchError(this.handleError<Degree[]>('getDegrees', []))
      );
  }
  getTitles(): Observable<Title[]> {
    return this.http.get<Title[]>(this.titleUrl)
      .pipe(
        catchError(this.handleError<Title[]>('getTitles', []))
      );
  }

  getDepartments(): Observable<Department[]> {
    return this.http.get<Department[]>(this.depUrl ).pipe(
      catchError(this.handleError<Department[]>('getDepartments', [])));
  }
  getTeachers(): Observable<Teacher[]> {
    return this.http.get<Teacher[]>(this.teacherUrl ).pipe(
      catchError(this.handleError<Teacher[]>('getTeachers', [])));
  }

  deleteCollege(college: College | number): Observable<Response> {
    const id = typeof college === 'number' ? college : college.id;
    return this.http.delete<Response>(this.collegeUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteCollege'))
    );
  }
  deleteGraduateProjectType(graduateProjectType: GraduateProjectType | number): Observable<Response> {
    const id = typeof graduateProjectType === 'number' ? graduateProjectType : graduateProjectType.id;
    return this.http.delete<Response>(this.projectTypeUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteGraduateProjectType'))
    );
  }
  deleteGraduateProject(gradProject: GraduateProject | number): Observable<Response> {
    const id = typeof gradProject === 'number' ? gradProject : gradProject;
    return this.http.delete<Response>(this.gradProjectUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteGradProject'))
    );
  }
  deleteGraduateProjectStatus(graduateProjectStatus: GraduateProjectStatus | number): Observable<Response> {
    const id = typeof graduateProjectStatus === 'number' ? graduateProjectStatus : graduateProjectStatus;
    return this.http.delete<Response>(this.projectStatusUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteGradProject'))
    );
  }
  deleteGraduateProjectCategory(graduateProjectCategory: GraduateProjectCategory | number): Observable<Response> {
    const id = typeof graduateProjectCategory === 'number' ? graduateProjectCategory : graduateProjectCategory;
    return this.http.delete<Response>(this.projectCategoryUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteProjectCategory'))
    );
  }
  deleteTitle(title: Title | number): Observable<Response> {
    const id = typeof title === 'number' ? title : title.id;
    return this.http.delete<Response>(this.titleUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteTitle'))
    );
  }

  deleteDegree(degree: Degree | number): Observable<Response> {
    const id = typeof degree === 'number' ? degree : degree.id;
    return this.http.delete<Response>(this.degreeUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteDegree'))
    );
  }

  deleteDepartment(department: Department | number): Observable<Response> {
    const id = typeof department === 'number' ? department : department.id;
    return this.http.delete<Response>(this.depUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteDepartment'))
    );
  }

  deleteTeacher(teacher: Teacher | number): Observable<Response> {
    const id = typeof teacher === 'number' ? teacher : teacher.id;
    return this.http.delete<Response>(this.teacherUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteTeacher'))
    );
  }
  /** POST: add a new college to the server */
  addCollege(college: { description: string; no: string;  remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.collegeUrl, college, httpOptions).pipe(
      catchError(this.handleError<any>('addCollege'))
    );
  }
  addGraduateProjectType(graduateProjectType: { description: string; no: string;  remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.projectTypeUrl, graduateProjectType, httpOptions).pipe(
      catchError(this.handleError<any>('addGraduateProjectType'))
    );
  }
  addGraduateProjectCategory(graduateProjectCategory: { description: string; no: string;  remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.projectCategoryUrl, graduateProjectCategory, httpOptions).pipe(
      catchError(this.handleError<any>('addProjectCategory'))
    );
  }
  /** POST: add a new college to the server */
  addDegree(degree: { description: string; no: string;  remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.degreeUrl, degree, httpOptions).pipe(
      catchError(this.handleError<any>('addDegree'))
    );
  }

  addTitle(title: { description: string; no: string;  remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.titleUrl, title, httpOptions).pipe(
      catchError(this.handleError<any>('addTitle'))
    );
  }
  addDepartment(department: Department1): Observable<Response> {
    return this.http.post<Response>(this.depUrl, department, httpOptions).pipe(
      catchError(this.handleError<any>('addDepartment'))
    );
  }

  addGraduateProjectStatus(graduateProjectStatus: { no: string; description: string; remarks: string }): Observable<Response> {
    return this.http.post<Response>(this.projectStatusUrl, graduateProjectStatus, httpOptions).pipe(
      catchError(this.handleError<any>('addGraduateProjectStatus'))
    );
  }
  addGraduateProject(graduateProject: GraduateProject): Observable<Response> {
    return this.http.post<Response>(this.gradProjectUrl, graduateProject, httpOptions).pipe(
      catchError(this.handleError<any>('addGraduateProject'))
    );
  }
  addTeacher(teacher: Teacher1): Observable<Response> {
    return this.http.post<Response>(this.teacherUrl, teacher, httpOptions).pipe(
      catchError(this.handleError<any>('addTeacher'))
    );
  }
  getGraduateProjectCategory(id: number): Observable<GraduateProjectCategory> {
    const url = this.projectCategoryUrl + '?id=' + id;
    return this.http.get<GraduateProjectCategory>(url).pipe(
      catchError(this.handleError<GraduateProjectCategory>(`getProjectStatus id=${id}`))
    );
  }
  getGraduateProjectType(id: number): Observable<GraduateProjectType> {
    const url = this.projectTypeUrl + '?id=' + id;
    return this.http.get<GraduateProjectType>(url).pipe(
      catchError(this.handleError<GraduateProjectType>(`getProjectType id=${id}`))
    );
  }
  /** GET college by id. Will 404 if id not found */
  getCollege(id: number): Observable<College> {
    const url = this.collegeUrl + '?id=' + id;
    return this.http.get<College>(url).pipe(
      catchError(this.handleError<College>(`getCollege id=${id}`))
    );
  }
  getGraduateProject(id: number): Observable<GraduateProject> {
    const url = this.gradProjectUrl + '?id=' + id;
    return this.http.get<GraduateProject>(url).pipe(
      catchError(this.handleError<GraduateProject>(`getGradProject id=${id}`))
    );
  }
  getGraduateProjectStatus(id: number): Observable<College> {
    const url = this.projectStatusUrl + '?id=' + id;
    return this.http.get<GraduateProjectStatus>(url).pipe(
      catchError(this.handleError<GraduateProjectStatus>(`getProjectCategory id=${id}`))
    );
  }
  getDegree(id: number): Observable<Degree> {
    const url = this.degreeUrl + '?id=' + id;
    return this.http.get<Degree>(url).pipe(
      catchError(this.handleError<Degree>(`getDegree id=${id}`))
    );
  }
  getTitle(id: number): Observable<Title> {
    const url = this.titleUrl + '?id=' + id;
    return this.http.get<Title>(url).pipe(
      catchError(this.handleError<Title>(`getTitle id=${id}`))
    );
  }
  getDep(id: number): Observable<Department> {
    const url = this.depUrl + '?id=' + id;
    return this.http.get<Department>(url).pipe(
      catchError(this.handleError<Department>(`getDep id=${id}`))
    );
  }

  getTeacher(id: number): Observable<Teacher> {
    const url = this.teacherUrl + '?id=' + id;
    return this.http.get<Teacher>(url).pipe(
      catchError(this.handleError<Teacher>(`getTeacher id=${id}`))
    );
  }

  getDepSch(id: number): Observable<Department[]> {
    const url = this.depUrl + '?paraType=school&id=' + id;
    return this.http.get<Department[]>(url).pipe(
      catchError(this.handleError<Department[]>(`getDepSch schoolId=${id}`))
    );
  }

  updateCollege(college: College): Observable<Response> {
    return this.http.put<Response>(this.collegeUrl, college, httpOptions).pipe(
      catchError(this.handleError<Response>('updateCollege'))
    );
  }
  updateGraduateProjectType(graduateProjectType: GraduateProjectType): Observable<Response> {
    return this.http.put<Response>(this.projectTypeUrl, graduateProjectType, httpOptions).pipe(
      catchError(this.handleError<Response>('updateCollege'))
    );
  }

  updateGraduateProject(gradProject: GraduateProject): Observable<Response> {
    return this.http.put<Response>(this.gradProjectUrl, gradProject, httpOptions).pipe(
      catchError(this.handleError<Response>('updateGradProject'))
    );
  }
  updateGraduateProjectCategory(graduateProjectCategory: GraduateProjectCategory): Observable<Response> {
    return this.http.put<Response>(this.projectCategoryUrl, graduateProjectCategory, httpOptions).pipe(
      catchError(this.handleError<Response>('updateProjectCategory'))
    );
  }
  updateGraduateProjectStatus(graduateProjectStatus: GraduateProjectStatus): Observable<Response> {
    return this.http.put<Response>(this.projectStatusUrl, graduateProjectStatus, httpOptions).pipe(
      catchError(this.handleError<Response>('updateProjectStatus'))
    );
  }
  updateDegree(degree: Degree): Observable<Response> {
    return this.http.put<Response>(this.degreeUrl, degree, httpOptions).pipe(
      catchError(this.handleError<Response>('updateDegree'))
    );
  }

  updateTitle(title: Title): Observable<Response> {
    return this.http.put<Response>(this.titleUrl, title, httpOptions).pipe(
      catchError(this.handleError<Response>('updateTitle'))
    );
  }
  updateDep(department: Department): Observable<Response> {
    return this.http.put<Response>(this.depUrl, department, httpOptions).pipe(
      catchError(this.handleError<Response>('updateDep'))
    );
  }

  updateTeacher(teacher: Teacher): Observable<Response> {
    return this.http.put<Response>(this.teacherUrl, teacher, httpOptions).pipe(
      catchError(this.handleError<Response>('updateTeacher'))
    );
  }
  checkUser(user: {username: string, password: string}): Observable<Response> {
    return this.http.post<Response>(this.userUrl, user).pipe(
      catchError(this.handleError<Response>('checkUser'))
    );
  }
  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  logOut() {
  }
}

